<template>
  <div>
    <Modal
        v-model="modal_1"
        title="任务详情信息"
        scrollable>
      <pre>
        {{ show_data }}
      </pre>
    </Modal>
    <Modal v-model="modal_2" title="修改机器参数" scrollable @on-ok="insert_machine_operation()">
      <div>
        <Form :model="show_data" label-position="left" :label-width="100">
          <FormItem label="Name">
            <Input v-model="show_data.name"></Input>
          </FormItem>
          <FormItem label="Tag">
            <Input v-model="show_data.tag"></Input>
          </FormItem>
          <FormItem label="Limit Process">
            <Input v-model="show_data.limit_process"></Input>
          </FormItem>
        </Form>
      </div>
    </Modal>
    <Modal
        v-model="modal_3"
        title="确定停机？"
        @on-ok="insert_machine_operation(status=-1)"
        scrollable>
    </Modal>
    <Card>
      <h2 slot='title'>机器负载情况 <span @click="get_machine_info()">
        <Icon type="ios-refresh-circle-outline">
        </Icon>
        </span></h2>
      <!--      <Button-->
      <!--          type="primary"-->
      <!--          slot="extra"-->
      <!--      >-->
      <!--        <Icon type="ios-download-outline"></Icon>-->
      <!--        数据上传程序-->
      <!--      </Button>-->
      <template slot="extra">
        30S 自动刷新
        <i-switch v-model="auto_refresh_machine_info">
        </i-switch>
      </template>
      <!--      <template slot="extra">-->
      <!--        <Switch>-->
      <!--          <span slot="open">开</span>-->
      <!--          <span slot="close">关</span>-->
      <!--        </Switch>-->
      <!--      </template>-->
      <Table stripe :columns="machine_column" :data="machine_data" :loading="loading">
        <!--        <template slot-scope="{ row, index }" slot="action">-->
        <template slot="action" slot-scope="{ row }">
          <Dropdown transfer>
            <Button type="primary"
                    @click="filter_machine_id=row.machine_id; change_time=Date.parse(new Date())">
              任务列表
              <Icon type="ios-arrow-down"></Icon>
            </Button>

            <DropdownMenu slot="list">
              <DropdownItem @click.native="filter_machine_id=row.machine_id; change_time=Date.parse(new Date())">任务列表
              </DropdownItem>
              <DropdownItem @click.native="modal_1 = true; show_data = row">显示详情</DropdownItem>
              <!-- <DropdownItem onclick="get_data()"> 提高优先级</DropdownItem> -->
              <DropdownItem @click.native="modal_2 = true; show_data = row">修改参数</DropdownItem>
              <DropdownItem v-if="row.status!==0" @click.native="show_data = row; insert_machine_operation(0);">暂停
              </DropdownItem>
              <DropdownItem v-if="row.status===0" @click.native="show_data = row; insert_machine_operation(1);">继续
              </DropdownItem>
              <DropdownItem @click.native=" show_data = row; modal_3 = true;">停机</DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </template>
      </Table>
    </Card>
    <br/>
    <all_job :filter_machine_id="filter_machine_id" :change_time="change_time">
    </all_job>
  </div>
</template>
<script>
import all_job from '@/components/all_job'

export default {
  name: "machine_info",
  components: {all_job},
  data() {
    return {
      auto_refresh_machine_info: false,
      modal_1: false,
      modal_2: false,
      modal_3: false,
      show_data: {},
      machine_column: [
        {
          title: 'Name',
          key: 'name',
          minWidth: 10,
          aligan: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "机器的名称",
                }
              })]
          },
          render: (h, params) => {
            return [h('span', params.row.name + '  '), h('Icon', {
              props: {type: "ios-alert"},
              attrs: {
                title: "id: " + params.row.machine_id,
              }
            })]
          }
        },
        {
          title: 'Tag',
          key: 'tag',
          tooltip: true,
          minWidth: 110,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "机器可以执行的任务种类",
                }
              })]
          }
        },
        {
          title: 'Cpu (n)',
          key: 'cpu_ratio',
          minWidth: 20,
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "CPU负载率和核心数目",
                }
              })]
          },
          render: (h, params) => {
            return h('span', params.row.cpu_ratio.toString()
                + '% ('
                + params.row.cpu_core.toString()
                + ')')
          }
        },
        {
          title: 'Memory (G)',
          key: 'memory_free',
          minWidth: 50,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "内存使用率和总数",
                }
              })]
          },
          render: (h, params) => {
            return h('span', (params.row.memory_used / 1000).toFixed(1).toString()
                + ' / '
                + ((params.row.memory_used + params.row.memory_free) / 1000).toFixed(1).toString())
          }
        },
        {
          title: 'Disk (G)',
          key: 'disk_free',
          minWidth: 30,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "硬盘使用率和容量",
                }
              })]
          },
          render: (h, params) => {
            return h('span', (params.row.disk_used / 1000).toFixed(1).toString()
                + ' / '
                + ((params.row.disk_used + params.row.disk_free) / 1000).toFixed(1).toString())
          }
        },
        {
          title: 'Process',
          key: 'limit_process',
          minWidth: 30,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "正在计算的进程数和进程上限",
                }
              })]
          },
          render: (h, params) => {
            return h('span', params.row.count_process.toString() + ' / ' + params.row.limit_process.toString())
          }
        }, {
          title: 'Status',
          key: 'limit_process',
          minWidth: 20,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "机器状态: 绿色正在运行；\n 黄色: 暂停;\n 灰色: 停机; \n 红色:超过5分钟未响应",
                }
              })]
          },
          render: (h, params) => {
            return h('div', {
                  attrs: {
                    id: "box"
                  },
                  class: {
                    'circle': true
                  },
                  style: {
                    'background-color': params.row.status === 1 ? "green" : params.row.status === 0 ? "#FFCC00" : params.row.status === 0 ? "black" : "red",
                  },
                },
                ""
            )
          }
        },
        {
          title: 'Action',
          slot: 'action',
          minWidth: 70,
          align: 'center'
        }
      ],
      machine_data: [
        {
          "count_process": 0,
          "cpu_core": 2,
          "cpu_ratio": 0,
          "create_time": "2020-08-30 20:57:53",
          "disk_free": 8261,
          "disk_used": 31714,
          "limit_process": 50,
          "machine_id": "189d2d00e49c11eaaf29837d79cfd7a7",
          "memory_free": 142,
          "memory_used": 1773,
          "name": "my",
          "platform": "linux",
          "status": 1,
          "tag": "get_aqi, 1688, get_1688_mermber_id",
          "update_time": "2021-01-27 00:52:00"
        }

      ],
      loading: false,
      filter_machine_id: "",
      change_time: 0,
      new_machine_info: {tag: "", name: "", limit_process: ""}
    }
  },
  mounted() {
  }
  ,
  created() {
    this.get_machine_info()
    this.loop_fresh_data()
  },
  methods: {
    get_machine_info() {
      var that = this
      that.loading = true
      this.$axios.get('get_machine_info', { // 还可以直接把参数拼接在url后边
        params: {}
      }).then(function (res) {
        that.machine_data = res.data.data
        that.$Message.info('获取machine数据成功！')
        that.loading = false
      }).catch(function (error) {
        console.log(error)
      })
    },
    loop_fresh_data() {
      var that = this
      window.setInterval(() => {
        setTimeout(() => {
          ///调取接口获取数据
          if (that.auto_refresh_machine_info) {
            this.get_machine_info()
          }
        }, 0)
      }, 30000)
    },
    insert_machine_operation(status = 1) {
      var that = this
      this.$axios.post('insert_machine_operation', { // 还可以直接把参数拼接在url后边
        machine_id: that.show_data.machine_id,
        operation: {status: status, machine_info: that.show_data}
      }).then(function (res) {
        that.$Message.info('修改参数已经提交！请等待30秒刷新！')
      }).catch(function (error) {
        console.log(error)
      })
    }

  }

}
</script>
