<template >
  <div>
    <Modal
        v-model="modal1"
        title="任务详情信息"
        scrollable>
      <pre>
        {{ show_data }}
      </pre>
    </Modal>
    <Card>
      <h2 slot='title'>任务运行列表
        <span @click="refresh_data()">
         <Icon type="ios-refresh-circle-outline">
         </Icon>
        </span>
      </h2>
      <template slot="extra">
        30S 自动刷新
        <i-switch v-model="auto_refresh_all_job">
        </i-switch>
      </template>
      <!--
      <Button
        type="primary"
        slot="extra"
      >
      <Icon type="ios-download-outline"></Icon>
      数据上传程序
      </Button>-->
      <Form :model="filter_data" :label-width="50">
        <Row :gutter="8">
          <Col span="5">
            <FormItem label="Batch" :label-width="50">
              <Select v-model="filter_data.batch" clearable
                      :placeholder="filter_data.batch ? filter_data.batch.toString():'选择任务批次' "
                      style="width:90%;float:left">
                <Option v-for="item in distinct_select.batch" :value="item" :key="item">{{ item }}</Option>
              </Select>
            </FormItem>
          </Col>
          <Col span="5">
            <FormItem label="Type" :label-width="50">
              <Select v-model="filter_data.job_type" clearable
                      :placeholder="filter_data.job_type ? filter_data.job_type.toString() : '选择任务类型'">
                <Option v-for="item in distinct_select.job_type" :value="item" :key="item">{{ item }}</Option>
              </Select>
            </FormItem>
          </Col>
          <Col span="6">
            <FormItem label="machine_id" :label-width="80">
              <Select v-model="filter_data.machine_id" clearable
                      :placeholder="filter_data.machine_id ? filter_data.machine_id.toString() : '选择查询机器'">
                <Option v-for="item in distinct_select.machine_id" :value="item" :key="item">{{ item }}</Option>
              </Select>
            </FormItem>
          </Col>
          <Col span="5">
            <FormItem label="Status">
              <Select v-model="filter_data.status" clearable
                      :placeholder="filter_data.status ? filter_data.status.toString() : '选择任务状态'">
                <Option v-for="item in distinct_select.status" :value="item" :key="item">{{ item }}</Option>
              </Select>
            </FormItem>
          </Col>
          <Col span="3">
            <FormItem style="float:right">
              <Button type="info" @click="get_data()">查询</Button>
            </FormItem>
          </Col>
        </Row>
      </Form>

      <Table stripe :columns="job_column" :data="job_data" :loading="loading">
        <!--        <template slot-scope="{ row, index }" slot="action">-->
        <template slot="action" slot-scope="{ row }">
          <Dropdown style="margin-left: 0px" transfer>
            <Button type="primary" @click="modal1 = true; show_data = row">
              显示详情
              <Icon type="ios-arrow-down"></Icon>
            </Button>
            <DropdownMenu slot="list">
              <DropdownItem @click.native="modal1 = true; show_data = row"> 显示详情</DropdownItem>
              <!-- <DropdownItem onclick="get_data()"> 提高优先级</DropdownItem> -->
              <DropdownItem :disabled="(row.status !== 1)&&(row.status !== 0)" title="当status=0/1(正在跑)时才能停止任务" @click.native="kill_job(job_id=row.id)">
                停止
              </DropdownItem>
              <DropdownItem :disabled="row.status === 1" title="只有在status!=1/2(计算中)的状态才能删除任务"
                            @click.native="delete_job(job_id=row.id)">删除
              </DropdownItem>
              <DropdownItem :disabled="row.status === 0?true:row.status === 1" title="只有在status!=0/1(不在跑)的状态才能重跑"
                            @click.native="rerun_job(job_id=row.id)">重跑
              </DropdownItem>
              <DropdownItem @click.native="copy_job(job_id=row.id)">复制重提</DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </template>
        <!--        <template slot-scope="{ row, index }" slot="action">
                  <Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">View</Button>
                  <Button type="error" size="small" @click="remove(index)">Delete</Button>
                </template>-->
      </Table>
      <Page :current=current_page :total=total_data_size show-sizer show-total :page-size=page_size
            @on-change="change_page" @on-page-size-change="change_page_size">
      </Page>
    </Card>
    <br/>
    <!--
    <Card>
      <h2 slot='title'>任务负载情况</h2>
      <Button
          type="primary"
          slot="extra"
      >
        <Icon type="ios-download-outline"></Icon>
        数据上传程序
      </Button>
      <Table stripe :columns="job_column" :data="job_data"></Table>
    </Card>-->
  </div>
</template>
<script>
export default {
  name: "home",
  props: {
    auto_refresh_all_job_flag: {required: false, default: false},
    filter_batch: {required: false, default: ''},
    filter_job_type: {required: false, default: ''},
    filter_machine_id: {required: false, default: ''},
    change_time: {required: false, default: 0}
  },
  data() {
    return {
      show_data: '',
      current_page: 1,
      total_data_size: 0,
      page_size: 10,
      loading: true,
      auto_refresh_all_job: this.auto_refresh_all_job_flag,
      modal1: false,
      job_column: [
        {
          title: 'Id',
          key: 'id',
          align: 'center',
          minWidth: 30,
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务ID",
                }
              })]
          }
        },
        {
          title: 'Batch',
          key: 'batch',
          align: 'center',
          minWidth: 50,
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务批次",
                }
              })]
          }
        },
        {
          title: 'Type',
          key: 'job_type',
          align: 'center',
          minWidth: 30,
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务类型",
                }
              })]
          }
        },
        {
          title: 'Count',
          key: 'return_count',
          align: 'center',
          minWidth: 30,
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务返回的数据条数",
                }
              })]
          }
        },
        {
          title: 'Speed (s)',
          key: 'spend_time',
          align: 'center',
          minWidth: 45,
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "完成任务的计算时间(秒)",
                }
              })]
          }
        },
        {
          title: 'Status',
          key: 'status',
          align: 'center',
          minWidth: 30,
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务状态:\n-2: 任务完成，回传时候出错；\n-1: 任务运行时出错；\n 0: 任务正在排队；\n 1: 任务正在计算 \n 2: 任务计算完成，正在回传结果 \n 3: 任务完成",
                }
              })]
          }
        },
        {
          title: 'Create',
          key: 'create_time',
          align: 'center',
          minWidth: 40,
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务的创建",
                }
              })]
          }
        },
        {
          title: 'Update',
          key: 'update_time',
          align: 'center',
          minWidth: 40,
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务状态更新时间",
                }
              })]
          }
        },
        {
          title: 'Action',
          slot: 'action',
          minWidth: 40,
          align: 'center'
        }
      ],
      job_data: [],
      distinct_select: {"status":[""], "machine_id":[""], "job_type":[""], "batch":[""]},
      filter_data: {
        batch: this.filter_batch,
        job_type: this.filter_job_type,
        machine_id: this.filter_machine_id,
        status: ""
      }
    }
  },
  created() {
    this.get_data()
    this.get_distinct_select()
    this.loop_fresh_data()
  },
  methods: {
    get_data(current_page = 1,
             page_size = 10,
             batch = this.filter_data.batch,
             job_type = this.filter_data.job_type,
             status = this.filter_data.status,
             machine_id = this.filter_data.machine_id) {
      var that = this
      that.loading = true
      this.$axios.get('get_job_list', { // 还可以直接把参数拼接在url后边
        params: {
          offset: (current_page - 1) * page_size,
          limit: page_size,
          batch: batch,
          job_type: job_type,
          status: status,
          machine_id: machine_id
        }
      }).then(function (res) {

        that.$Message.info('获取列表数据成功！')

        that.job_data = res.data.data
        that.total_data_size = res.data.count
        that.loading = false
      }).catch(function (error) {
        console.log(error)
        that.loading = false
      })
    },
    change_page(index) {
      this.current_page = index
      this.auto_refresh_all_job = false
      this.get_data(this.current_page, this.page_size)
    },
    change_page_size(page_size) {
      this.page_size = page_size
      this.get_data(this.current_page, this.page_size)
    },
    loop_fresh_data() {
      var that = this
      window.setInterval(() => {
        setTimeout(() => {
          ///调取接口获取数据
          if (that.auto_refresh_all_job) {
            this.get_data(this.current_page, this.page_size)
          }
        }, 0)
      }, 30000)
    },
    refresh_data() {
      this.get_data(this.current_page, this.page_size)
    },
    get_distinct_select() {
      var that = this
      this.$axios.get('get_distinct_select', { // 还可以直接把参数拼接在url后边
        params: {}
      }).then(function (res) {
        that.distinct_select = res.data
      }).catch(function (error) {
        console.log(error)
        that.loading = false
      })

    },
    delete_job(job_id='', machine_id='', job_type='', batch='') {
      var that = this
      that.loading = true
      this.$axios.post('delete_job', {
        job_id: job_id,
        machine_id:machine_id,
        job_type:job_type,
        batch:batch
      }).then(function (res) {
        that.$Message.info('删除任务成功！')
        that.loading = false
        that.refresh_data()
      }).catch(function (error) {
        console.log(error)
        that.loading = false
      })

    },
    rerun_job(job_id='', machine_id='', job_type='', batch='') {
      var that = this
      that.loading = true
      this.$axios.post('rerun_job', { // 还可以直接把参数拼接在url后边
        job_id: job_id,
        machine_id:machine_id,
        job_type:job_type,
        batch:batch
      }).then(function (res) {
        that.$Message.info('重跑任务提交成功！')
        that.loading = false
        that.refresh_data()
      }).catch(function (error) {
        console.log(error)
        that.loading = false
      })

    },
    copy_job(job_id='', machine_id='', job_type='', batch='') {
      var that = this
      that.loading = true
      this.$axios.post('copy_job', {
        job_id: job_id,
        machine_id:machine_id,
        job_type:job_type,
        batch:batch
      }).then(function (res) {
        that.$Message.info('复制任务提交成功！')
        that.loading = false
        that.refresh_data()
      }).catch(function (error) {
        console.log(error)
        that.loading = false
      })

    },
    kill_job(job_id='', machine_id='', job_type='', batch='') {
      var that = this
      that.loading = true
      this.$axios.post('kill_job', {
        job_id: job_id,
        machine_id:machine_id,
        job_type:job_type,
        batch:batch
      }).then(function (res) {
        that.$Message.info('强制停止任务提交成功！')
        that.loading = false
        that.refresh_data()
      }).catch(function (error) {
        console.log(error)
        that.loading = false
      })

    }


  },
  watch: {
    filter_batch: {
      handler(new_value, old_value) {
        this.filter_data.batch = new_value
      }
    },
    filter_job_type: {
      handler(new_value, old_value) {
        this.filter_data.job_type = new_value
      }
    },
    filter_machine_id: {
      handler(new_value, old_value) {
        this.filter_data.machine_id = new_value
      }
    },
    change_time: {
      handler(new_value, old_value) {
        this.filter_data.status = ''
        this.get_data()
      }
    }

  }

}
// {
//   "batch": "2021-01-26",
//     "create_time": "2021-01-26 09:46:01",
//     "id": 168273,
//     "input_data": "[('2021-01-25 00:00:00', '2021-01-26 00:00:00', '永州'), ('2021-01-26 00:00:00', '20'2021-01-28 00:00:00', '永州'), ('2021-01-14 00:00:00', '2021-01-15 00:00:00', '扬州'), ('2021-01-17 00:00:00:00', '2021-01-22 00:00:00', '扬州'), ('2021-01-26 00:00:00', '2021-01-27 00:00:00', '扬州')]",
//     "job_type": "get_aqi",
//     "limit_count": 0,
//     "machine_id": "189d2d00e49c11eaaf29837d79cfd7a7",
//     "priority": 0,
//     "result": "HTTPSConnectionPool(host='www.aqistudy.cn', port=443): Max retries exceeded with url: /hnot connect to proxy.', RemoteDisconnected('Remote end closed connection without response')))",
//     "return_count": 0,
//     "spend_time": 5,
//     "status": -1,
//     "tag": "get_aqi",
//     "update_time": "2021-01-26 09:55:37"
// }
</script>
