<template>
  <div>
    <Modal
        v-model="modal_1"
        title="任务详情信息"
        scrollable>
      <pre>
        {{ show_data }}
      </pre>
    </Modal>
    <Modal v-model="modal_4" title="删除任务记录" scrollable ok-text="确认删除"
           @on-ok="$refs.all_job_0.delete_job(job_id='', machine_id='', job_type=show_data.job_type,batch=show_data.batch)">
      <h4>确认删除后，将会删除该批次相关的任务记录！</h4>
    </Modal>
    <Card>
      <h2 slot='title'>任务批次统计
        <span @click="get_job_batch()">
        <Icon type="ios-refresh-circle-outline">
        </Icon>
        </span>
        <!--        <Select v-model="model1" style="width:200px">-->
        <!--          <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>-->
        <!--        </Select>-->
      </h2>
      <template slot="extra">
        30S 自动刷新
        <i-switch v-model="auto_refresh_job_batch">
        </i-switch>
      </template>
      <!--      <Button
                type="primary"
                slot="extra"
            >
              <Icon type="ios-download-outline"></Icon>
              数据上传程序
            </Button>-->
      <Table stripe :columns="job_batch_column" :data="job_batch_data_show" :loading="loading">
        <template slot="action" slot-scope="{ row }">
          <Dropdown transfer>
            <Button type="primary"
                    @click="filter_batch=row.batch; filter_job_type=row.job_type; change_time=Date.parse(new Date())">
              任务列表
              <Icon type="ios-arrow-down"></Icon>
            </Button>
            <DropdownMenu slot="list">
              <DropdownItem @click.native="modal_1 = true; show_data = row">显示详情</DropdownItem>
              <!-- <DropdownItem onclick="get_data()"> 提高优先级</DropdownItem> -->
              <DropdownItem title="只有status=0/1(排队/计算)中的任务才能停止"
                            @click.native="$refs.all_job_0.kill_job(job_id='', machine_id='', job_type=row.job_type,batch=row.batch)">
                停止
              </DropdownItem>
              <DropdownItem title="只有status=3/-1/-2(完成/异常终止)的任务才能被重跑"
                            @click.native="$refs.all_job_0.rerun_job(job_id='', machine_id='', job_type=row.job_type,batch=row.batch)">
                重跑
              </DropdownItem>
              <DropdownItem title="只有status=3/-1/-2(完成/异常终止)的任务才能被删除"
                            @click.native="show_data=row;modal_4=true">删除
              </DropdownItem>
              <DropdownItem title="整组任务将会被重新复制" ref="all_job"
                            @click.native="$refs.all_job_0.copy_job(job_id='', machine_id='', job_type=row.job_type,batch=row.batch)">
                复制
              </DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </template>
      </Table>
      <Page :current=current_page :total=total_data_size show-sizer show-total :page-size=page_size
            @on-change="change_page" @on-page-size-change="change_page_size">
      </Page>
    </Card>
    <br/>
    <!--    <Card>-->
    <!--      <h2 slot='title'>任务详情信息 批次&#45;&#45;</h2>-->
    <!--      <Button-->
    <!--          type="primary"-->
    <!--          slot="extra"-->
    <!--      >-->
    <!--        <Icon type="ios-download-outline"></Icon>-->
    <!--        数据上传程序-->
    <!--      </Button>-->
    <!--      <Table stripe :columns="job_column" :data="job_data"></Table>-->
    <!--      <Page :total="100" show-sizer/>-->
    <!--    </Card>-->
    <all_job ref="all_job_0" :auto_refresh_all_job_flag=false :filter_batch="filter_batch"
             :filter_job_type="filter_job_type" :filter_machine_id="filter_machine_id" :change_time="change_time">
    </all_job>
  </div>
</template>
<script>
import all_job from '@/components/all_job'

export default {
  name: "job_batch",
  components: {all_job},
  data() {
    return {
      change_time: 0,
      filter_batch: '',
      filter_job_type: '',
      filter_machine_id: '',
      modal_1: false,
      modal_2: false,
      modal_4: false,
      show_data: {},
      current_page: 1,
      auto_refresh_job_batch: false,
      total_data_size: 150,
      job_batch_data_show: [],
      page_size: 10,
      loading: false,
      job_batch_column: [
        {
          title: 'Batch',
          key: 'batch',
          minWidth: 50,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务批次",
                }
              })]
          }
        },
        {
          title: 'Job_type',
          key: 'job_type',
          minWidth: 50,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务类型",
                }
              })]
          }
        },
        {
          title: 'Count',
          key: 'count',
          minWidth: 50,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务返回的数据条数",
                }
              })]
          }
        },
        {
          title: 'Job',
          key: 'failed_task',
          minWidth: 100,
          align: 'center',
          render: (h, params) => {
            return [h('span', (params.row.finished_task.toString() / (params.row.finished_task
                + params.row.failed_task
                + params.row.waiting_task
                + params.row.working_task + 0.01) * 100).toFixed(2).toString() + '%' + '(' +
                (params.row.finished_task
                    + params.row.failed_task
                    + params.row.waiting_task
                    + params.row.working_task).toString()
                + ')'), h('Icon', {
              props: {type: "ios-alert"},
              attrs: {
                title: "finished: " + params.row.finished_task
                    + '; waiting: ' + params.row.waiting_task
                    + '; working: ' + params.row.working_task
                    + '; failed: ' + params.row.failed_task,
              }
            })]
          },
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务的完成情况",
                }
              })]
          }
        },
        {
          title: 'Speed (s)',
          key: 'spend_time',
          minWidth: 50,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "完成任务的平均计算时间(秒)",
                }
              })]
          }
        },
        {
          title: 'Update_time',
          key: 'update_time',
          minWidth: 100,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务状态更新时间",
                }
              })]
          }
        },
        {
          title: 'Action',
          slot: 'action',
          minWidth: 110,
          align: 'center'
        }
      ],
      job_batch_data: [
        {
          "batch": "",
          "begin_time": "",
          "count": 0,
          "failed_task": 0,
          "finished_task": 0,
          "job_type": "",
          "spend_time": 0,
          "tag": "",
          "update_time": "",
          "waiting_task": 0,
          "working_task": 0
        }
      ],
    }
  },
  mounted() {
  },
  created() {
    this.get_job_batch()
    this.loop_fresh_data()
  },
  methods: {
    get_job_batch() {
      var that = this
      that.loading = true
      this.$axios.get('get_job_summary', { // 还可以直接把参数拼接在url后边
        params: {}
      }).then(function (res) {
        that.job_batch_data = res.data.data
        that.total_data_size = that.job_batch_data.length
        that.job_batch_data_show = that.job_batch_data.slice((that.current_page - 1) * that.page_size,
            that.current_page * that.page_size)
        that.$Message.info('获取summary数据成功！')
        that.loading = false
        // console.log(that.job_batch_data.length)
      }).catch(function (error) {
        console.log(error)
      })
    },
    change_page(index) {
      var that = this
      that.job_batch_data_show = that.job_batch_data.slice((index - 1) * that.page_size, index * that.page_size)
    },
    change_page_size(page_size) {
      this.page_size = page_size
      this.job_batch_data_show = this.job_batch_data.slice(0, this.page_size)
    },
    loop_fresh_data() {
      var that = this
      window.setInterval(() => {
        setTimeout(() => {
          ///调取接口获取数据
          if (that.auto_refresh_job_batch) {
            this.get_job_batch()
          }
        }, 0)
      }, 30000)
    },
  }

}
</script>
