import Vue from 'vue'
import Router from 'vue-router'
// import statistics from '@/components/statistics'
import home from '@/components/home'
import job_batch from '@/components/job_batch'
import machine_info from '@/components/machine_info'
import job_type from '@/components/job_type'
import all_job from '@/components/all_job'
import HelloWorld from '@/components/HelloWorld'
// import fs from 'fs'
Vue.use(Router)

export default new Router({
    mode: "hash",
    routes: [{
        path: '/',
        name: '/',
        component: home
    }, {
        path: '/job_batch',
        name: 'job_batch',
        component: job_batch
    }, {
        path: '/machine_info',
        name: 'machine_info',
        component: machine_info
    }, {
        path: '/machine_info',
        name: 'machine_info',
        component: machine_info
    }, {
        path: '/job_type',
        name: 'job_type',
        component: job_type
    }, {
        path: '/all_job',
        name: 'all_job',
        component: all_job
    },
    ]
})

