<template>
  <div>

    <br/>
    <Row :gutter="8" type="flex" justify="center" align="middle">
      <Col span="24">
        <h2>任务负载情况 (最近24小时：回收 {{ history_job.summary_data.count }} 条数据) </h2>
        <Card>
          <Row :gutter="16" style="color:#fff">
            <Col span="6">
              <div class="show_card" style="background: #19be6b">
                <p>成功任务</p>
                <h4>{{ history_job.summary_data.finished_task }}</h4>
              </div>
            </Col>
            <Col span="6">
              <div class="show_card" style="background: #2db7f5;">
                <p>进行任务</p>
                <h4>{{ history_job.summary_data.working_task }}</h4>
              </div>
            </Col>
            <Col span="6">
              <div class="show_card" style="background: #ff9900">
                <p>排队任务</p>
                <h4>{{ history_job.summary_data.waiting_task }}</h4>
              </div>
            </Col>
            <Col span="6">
              <div class="show_card" style="background: #ed4014">
                <p>失败任务</p>
                <h4>{{ history_job.summary_data.failed_task }}</h4>
              </div>
            </Col>
          </Row>

          <div id="echart_0" style="width:100%;height: 400px">
          </div>
        </Card>
      </Col>

      <!--      <Col span="12">-->
      <!--        <Card>-->
      <!--          <h2 slot='title'>任务负载情况 （24小时） 折线图</h2>-->
      <!--          <Table stripe :columns="machine_column" :data="machine_data"></Table>-->
      <!--        </Card>-->
      <!--      </Col>-->
      <!--      <Col span="10">-->
      <!--        <Card>-->

      <!--        </Card>-->
      <!--      </Col>-->
    </Row>
  </div>
</template>
<script>
export default {
  name: "home",
  data() {
    return {
      history_job: {series: [], data: [], title: [], summary_data: {}},
      tmp_data: 120,
      machine_column: [
        {
          title: 'Name',
          key: 'name'
        },
        {
          title: 'Age',
          key: 'age'
        },
        {
          title: 'Address',
          key: 'address'
        }
      ],
      job_column: [
        {
          title: 'Name',
          key: 'name'
        },
        {
          title: 'Age',
          key: 'age'
        },
        {
          title: 'Address',
          key: 'address'
        }
      ],
      machine_data: [
        {
          name: 'John Brown',
          age: 18,
          address: 'New York No. 1 Lake Park',
          date: '2016-10-03'
        }
      ],
      job_data: [
        {
          name: 'John Brown',
          age: 18,
          address: 'New York No. 1 Lake Park',
          date: '2016-10-03'
        }
      ],
      show_key: {
        finished_task: {color: "#19be6b"},
        working_task: {color: "#2db7f5"},
        waiting_task: {color: "#ff9900"},
        failed_task: {color: "#ed4014"},
      }
    }
  },
  mounted() {
    this.get_history_job();
  },
  methods: {
    draw_line() {
      var that = this;
      var chartDom = document.getElementById('echart_0');
      var myChart = this.$echarts.init(chartDom);
      var option;
      option = {
        tooltip: {
          trigger: 'axis',
          formatter(params) {
            var tmp = "回收数据: " + that.history_job.front_end_data['count']['data'][params[0]['dataIndex']] + " 条"

            for (var tmp_key in params) {
              var tmp_data = params[tmp_key]
              tmp = tmp + '</br>' + tmp_data.seriesName + ": " + tmp_data.value
            }
            tmp = tmp + "</br>" + "任务类型: " + that.history_job.front_end_data['job_type']['data'][params[0]['dataIndex']]
            return tmp
          },
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: this.history_job.title
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: this.history_job.series
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: this.history_job.data
      };
      option && myChart.setOption(option);
    },
    get_history_job() {
      /*{
        name: '视频广告',
            type: 'bar',
          stack: '广告',
          emphasis: {
        focus: 'series'
      },
        data: [150, 232, 201, 154, 190, 330, 410]
      }*/
      var that = this
      this.$axios.get('get_history_job', { // 还可以直接把参数拼接在url后边
        params: {}
      }).then(function (res) {
        that.history_job.series = res.data.series
        that.history_job.title = res.data.title
        that.history_job.summary_data = res.data.summary_data
        that.history_job.front_end_data = res.data.front_end_data
        that.history_job.data = []
        for (let key in that.show_key) {
          var tmp_dic = res.data.front_end_data[key]
          tmp_dic['label'] = {"show": true}
          tmp_dic['itemStyle'] = {"color": that.show_key[key]['color']}
          that.history_job.data.push(tmp_dic)
        }
        that.draw_line()
      }).catch(function (error) {
        console.log(error)
        that.loading = false
      })

    },
  }

}
</script>
