<template>
  <div id='app' class="layout">
    <Layout>
      <Header>
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div class="layout-logo"></div>
          <div class="layout-nav">
            <MenuItem name="1">
              <Icon type="ios-analytics"></Icon>
              用户
            </MenuItem>
            <MenuItem name="2">
              <Icon type="ios-paper"></Icon>
              退出
            </MenuItem>
          </div>
        </Menu>
      </Header>
      <Layout>
        <Sider hide-trigger :style="{background: '#fff'}" style="min-height: 600px">

          <Menu active-name="1-2" theme="light" width="auto" :open-names="['2']" >
            <MenuItem name="1-1" :to="{name:'/'}">
              <Icon type="ios-aperture"/>
              主页
            </MenuItem>
            <Submenu name="2">
              <template slot="title">
                <Icon type="ios-keypad"></Icon>
                任务情况
              </template>
              <MenuItem name="2-1" :to="{name:'all_job'}">全部任务</MenuItem>
              <MenuItem name="2-2" :to="{name:'job_batch'}">不同批次</MenuItem>
              <MenuItem name="2-3" :to="{name:'machine_info'}">不同机器</MenuItem>
              <MenuItem name="2-4" :to="{name:'job_type'}">不同类型</MenuItem>
            </Submenu>
            <Submenu name="3">
              <template slot="title">
                <Icon type="ios-analytics"></Icon>
                其他
              </template>
              <MenuItem name="3-1">帮助信息</MenuItem>
              <MenuItem name="3-2">使用文档</MenuItem>
              <MenuItem name="3-3">创作团队</MenuItem>
            </Submenu>
          </Menu>
        </Sider>
        <Layout :style="{padding: '10px 24px 24px'}">
          <!--
          <Breadcrumb :style="{margin: '24px 0'}">
            <BreadcrumbItem>Home</BreadcrumbItem>
            <BreadcrumbItem>Components</BreadcrumbItem>
            <BreadcrumbItem>Layout</BreadcrumbItem>
          </Breadcrumb>-->
          <Content :style="{ minHeight: '280px', background: '#fff', textAlign: 'center'}">
            <router-view/>
          </Content>
        </Layout>
      </Layout>
    </Layout>
  </div>
</template>
<script>
export default {
  name: 'App',
  data() {
    return {
      isCollapsed: false
    }
  },
  computed: {
    rotateIcon() {
      return [
        'menu-icon',
        this.isCollapsed ? 'rotate-icon' : ''
      ];
    },
    menuitemClasses() {
      return [
        'menu-item',
        this.isCollapsed ? 'collapsed-menu' : ''
      ]
    }
  },
  methods: {
    collapsedSider() {
      this.$refs.side1.toggleCollapse();
    },
    get_data() {
      var that = this
      this.$axios.get('get_job_list', { // 还可以直接把参数拼接在url后边
        params: {}
      }).then(function (res) {
        that.job_data = res.data.data
      }).catch(function (error) {
        console.log(error)
      })
    }
  }
}
</script>
<style scoped>
.layout {
  border: 1px solid #d7dde4;
  background: #f5f7f9;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}

.layout-logo {
  width: 100px;
  height: 30px;
  background: #5b6270;
  border-radius: 3px;
  float: left;
  position: relative;
  top: 15px;
  left: 20px;
}

.layout-nav {
  /* width: 420px; */
  margin: 0 auto;
  margin-right: 20px;
  float: right;
}
</style>
