<template>
  <div>
    <Modal
        v-model="modal_1"
        title="任务详情信息"
        scrollable>
      <pre>
        {{ show_data }}
      </pre>
    </Modal>
    <Modal v-model="modal_2" title="提交新任务" scrollable @on-ok="insert_job(job_type=new_job.job_type,
    input_data=new_job.input_data, limit_count = new_job.limit_count, batch = new_job.batch,
    tag = new_job.tag, priority = new_job.priority)">
      <div>
        <Form :model="new_job" label-position="left" :label-width="100">
          <FormItem label="job_type" required>
            <Input v-model="new_job.job_type" readonly></Input>
          </FormItem>
          <FormItem label="input_data" required>
            <Input v-model="new_job.input_data" type="textarea" :rows="4" placeholder="任务的输入参数"></Input>
          </FormItem>
          <FormItem label="Tag">
            <Input v-model="new_job.tag" placeholder="可以跑的机器的标签。 默认为 job_type"></Input>
          </FormItem>
          <FormItem label="limit_count">
            <Input v-model="new_job.limit_count" placeholder="任务的失败重跑次数。 默认为 0"></Input>
          </FormItem>
          <FormItem label="batch">
            <Input v-model="new_job.batch" placeholder="任务批次，可用来快速定位任务。 默认为 当天 年-月-日"></Input>
          </FormItem>
          <FormItem label="priority">
            <Input v-model="new_job.priority" placeholder="任务优先级,0-999 越大表示优先级越高。 默认为 0"></Input>
          </FormItem>
        </Form>
      </div>
    </Modal>
    <Modal v-model="modal_3" title="更新任务文件" scrollable>
      <div>
        <Form :model="new_job_type" label-position="left" :label-width="100">
          <FormItem label="Job_type" required>
            <Input v-model="new_job_type.job_type"></Input>
          </FormItem>
          <!--
          <FormItem label="Version">
            <Input v-model="new_job_type.version" placeholder="任务版本，初始化任务为1，每次更新会自动 +1。"></Input>
          </FormItem>
          -->
          <FormItem>
            <Upload action="/upload_file" :data="new_job_type" :on-success="handle_success">
              <Button icon="ios-cloud-upload-outline">上传任务文件</Button>
            </Upload>
          </FormItem>
          <FormItem>
            <Button type="info" :disabled="!upload_status" @click="update_job_type(new_job_type.job_type)">更新任务种类
            </Button>
          </FormItem>
        </Form>
      </div>
    </Modal>
    <Modal v-model="modal_4" title="删除任务文件" scrollable ok-text="确认删除" @on-ok="delete_job_type(row_data.job_type)">
      <h4>确认删除后，将会删除该任务的文件，但不会删除该任务的运行记录！</h4>
    </Modal>
    <Card>
      <h2 slot='title'>任务类型统计
        <span @click="get_job_data()">
        <Icon type="ios-refresh-circle-outline">
        </Icon>
        </span>
        <!--        <Select v-model="model1" style="width:200px">-->
        <!--          <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>-->
        <!--        </Select>-->
      </h2>
      <template slot="extra">
        <Button type="info" @click="modal_3=true;upload_status=false">增加任务类型</Button>
        30S 自动刷新
        <i-switch v-model="auto_refresh_job_type">
        </i-switch>
      </template>
      <Table stripe :columns="job_type_column" :data="job_type_data">
        <template slot="action" slot-scope="{ row }">
          <Dropdown transfer>
            <Button type="primary"
                    @click="filter_job_type=row.job_type; change_time=Date.parse(new Date())">
              任务列表
              <Icon type="ios-arrow-down"></Icon>
            </Button>
            <DropdownMenu slot="list">
              <DropdownItem @click.native="modal_1 = true; show_data = row">显示详情</DropdownItem>
              <DropdownItem target="_blank" title="下载任务文件">
                <a :href="'get_job_file?job_type=' + row.job_type">下载文件</a>
              </DropdownItem>
              <DropdownItem @click.native="modal_3=true;upload_status=false;new_job_type.job_type=row.job_type"
                            title="更新任务文件">
                更新文件
              </DropdownItem>
              <DropdownItem title="添加一个任务"
                            @click.native="
                            new_job.job_type=row.job_type;
                            modal_2=true">
                添加任务
              </DropdownItem>
              <DropdownItem title="删除任务"
                            @click.native="
                            row_data=row;
                            modal_4=true">
                删除任务
              </DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </template>
      </Table>
    </Card>
    <br/>
    <all_job ref="all_job_0" :auto_refresh_all_job_flag=false :filter_batch="filter_batch"
             :filter_job_type="filter_job_type" :filter_machine_id="filter_machine_id" :change_time="change_time">
    </all_job>
  </div>
</template>
<script>
import all_job from "@/components/all_job";

export default {
  name: "job_type",
  components: {all_job},
  data() {
    return {
      job_type_column: [
        {
          title: 'Job_type',
          key: 'job_type',
          minWidth: 50,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务类型",
                }
              })]
          }
        },
        {
          title: 'Version',
          key: 'version',
          minWidth: 50,
          align: 'center',
          render: (h, params) => {
            return [h('span', params.row.version), h('Icon', {
              props: {type: "ios-alert"},
              attrs: {
                title: " Create_time:   " + params.row.version_create_time
                    + '\nUpdate_time: ' + params.row.version_update_time
              }
            })]
          },
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务的版本信息（每次更新会自动+1）",
                }
              })]
          }
        },
        {
          title: 'Count',
          key: 'count',
          minWidth: 50,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务返回的数据条数",
                }
              })]
          }
        },
        {
          title: 'Job',
          key: 'failed_task',
          minWidth: 100,
          align: 'center',
          render: (h, params) => {
            return [h('span', (params.row.finished_task.toString() / (params.row.finished_task
                + params.row.failed_task
                + params.row.waiting_task
                + params.row.working_task + 0.01) * 100).toFixed(2).toString() + '%' + '(' +
                (params.row.finished_task
                    + params.row.failed_task
                    + params.row.waiting_task
                    + params.row.working_task).toString()
                + ')'), h('Icon', {
              props: {type: "ios-alert"},
              attrs: {
                title: "finished: " + params.row.finished_task
                    + '; waiting: ' + params.row.waiting_task
                    + '; working: ' + params.row.working_task
                    + '; failed: ' + params.row.failed_task,
              }
            })]
          },
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务的完成情况",
                }
              })]
          }
        },
        {
          title: 'Speed (s)',
          key: 'spend_time',
          minWidth: 50,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "完成任务的平均计算时间(秒)",
                }
              })]
          }
        },
        {
          title: 'Update_time',
          key: 'update_time',
          minWidth: 100,
          align: 'center',
          renderHeader: (h, params) => {
            return [h('span', params.column.title),
              h('Icon', {
                props: {type: "ios-alert"},
                attrs: {
                  title: "任务状态更新时间",
                }
              })]
          }
        },
        {
          title: 'Action',
          slot: 'action',
          minWidth: 110,
          align: 'center'
        }
      ],
      job_type_data: [],
      modal_1: false,
      modal_2: false,
      modal_3: false,
      modal_4: false,
      row_data: {},
      upload_status: false,
      total_data_size: 0,
      show_data: "",
      filter_batch: "",
      filter_job_type: "",
      filter_machine_id: "",
      change_time: 0,
      auto_refresh_job_type: false,
      new_job: {},
      new_job_type: {"job_type": ''}
    }
  },
  mounted() {
    this.get_job_data()
    this.loop_fresh_data()
  },
  methods: {
    get_job_data() {
      var that = this
      this.$axios.get('get_job_summary', { // 还可以直接把参数拼接在url后边
        params: {batch: -1}
      }).then(function (res) {
        that.job_type_data = res.data.data
        that.total_data_size = that.job_type_data.length
        that.get_all_job_type()
        // console.log(that.job_batch_data.length)
      }).catch(function (error) {
        console.log(error)
      })
    },
    get_all_job_type() {
      var that = this
      that.loading = true
      this.$axios.get('get_all_job_type', { // 还可以直接把参数拼接在url后边
        params: {}
      }).then(function (res) {
        var tmp_job_file = res.data.data
        var tmp_data = []
        for (let i in that.job_type_data) {
          var line = that.job_type_data[i]
          if (tmp_job_file[line['job_type']]) {
            tmp_job_file[line['job_type']]['tag'] = line['tag']
            tmp_job_file[line['job_type']]['batch'] = line['batch']
            tmp_job_file[line['job_type']]['count'] = line['count']
            tmp_job_file[line['job_type']]['spend_time'] = line['spend_time']
            tmp_job_file[line['job_type']]['failed_task'] = line['failed_task']
            tmp_job_file[line['job_type']]['waiting_task'] = line['waiting_task']
            tmp_job_file[line['job_type']]['working_task'] = line['working_task']
            tmp_job_file[line['job_type']]['finishedr_task'] = line['finished_task']
            // line['version'] = tmp_job_file[line['job_type']]['version']
            // line['version_create_time'] = tmp_job_file[line['job_type']]['create_time']
            // line['version_update_time'] = tmp_job_file[line['job_type']]['update_time']
          }
        }
        for (var key in tmp_job_file) {
          tmp_data.push(tmp_job_file[key])
        }
        that.job_type_data = tmp_data;

        that.$Message.info('获取 Job_type 数据成功！')
        that.loading = false
        // console.log(that.job_batch_data.length)
      }).catch(function (error) {
        console.log(error)
      })

    },
    insert_job(job_type = '', input_data = '', limit_count = 0, batch = '', tag = '', priority = 0) {
      var that = this
      this.$axios.post('insert_job', { // 还可以直接把参数拼接在url后边
        job_type: job_type,
        input_data: input_data,
        limit_count: limit_count,
        batch: batch,
        tag: tag,
        priority: priority
      }).then(function (res) {
        that.$Message.info('修改参数已经提交！请等待30秒刷新！')
      }).catch(function (error) {
        console.log(error)
      })
    },
    update_job_type(job_type = '') {
      var that = this
      this.$axios.get('update_job_file_status', { // 还可以直接把参数拼接在url后边
        params: {job_type: job_type}
      }).then(function (res) {
        that.$Message.info('任务更新已经提交！请等待10秒刷新！')
      }).catch(function (error) {
        console.log(error)
      })
    },
    handle_success(res, file) {
      this.upload_status = true;
      this.$message.info('上传成功')
    },
    loop_fresh_data() {
      var that = this
      window.setInterval(() => {
        setTimeout(() => {
          ///调取接口获取数据
          if (that.auto_refresh_job_type) {
            this.get_job_data()
          }
        }, 0)
      }, 30000)
    },
    delete_job_type(job_type = '') {
      var that = this
      this.$axios.get('delete_job_file', { // 还可以直接把参数拼接在url后边
        params: {job_type: job_type}

      }).then(function (res) {
        that.get_job_data()
        that.$Message.info('您已经提交删除！请等待10秒刷新！')
      }).catch(function (error) {
        console.log(error)
      })

    }
  }

}
</script>
